@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700;800&display=swap);
#current-weather {
  display: grid;
}

#current-weather-main {
  display: grid;
  grid-template-columns: 0.75fr 1fr;
  color: #fff;
}

#current-weather-main-left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#current-weather-main img {
  width: 12rem;
  height: auto;
}

#current-weather-main-right {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#current-weather-temp {
  font-size: 4rem;
  margin-bottom: 1rem;
}

#current-weather-details {
  display: grid;

  grid-gap: 2rem;

  gap: 2rem;
  align-content: center;
  justify-items: center;
  color: #fff;
}

.detail-box {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detail {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
}

.detail-label {
  color: #aaa;
  font-size: 1rem;
}

.forecast-grid {
  width: 100%;
  display: grid;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.forecast-grid-item {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-content: center;
  justify-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  color: #fff;
}

.forecast-grid-item img {
  width: 6rem;
  height: auto;
}

.forecast-detail-box {
  display: grid;
  align-content: center;
  justify-items: center;
  grid-gap: 0.25rem;
  gap: 0.25rem;
}

.forecast-detail-name {
  font-size: 1.1rem;
  border-top: 0.1rem solid #aaa;
}

.forecast-detail-label {
  font-size: 0.9rem;
  color: #aaa;
}

@media (min-width: 576px) {
  .forecast-grid {
    grid-template-columns: 1fr;
  }

  .forecast-grid-item {
    grid-template-columns: repeat(5, 1fr);
  }
}

@media (min-width: 768px) {
}

@media (min-width: 992px) {
  .forecast-grid {
    grid-template-columns: repeat(7, 1fr);
  }

  .forecast-grid-item {
    grid-template-columns: 1fr;
  }
}

@media (min-width: 1200px) {
}

#weather {
  width: 100%;
}

#weather-header {
  display: flex;
  flex-direction: column;
}

#weather-location {
  font-size: 2rem;
  font-weight: 900;
  color: #fff;
  margin-bottom: 1rem;
}

#weather-date {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

#weather-description {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
}
#forecast-switches {
  display: grid;
  align-content: center;
  margin: 2rem;
}

#forecast {
  width: 100%;
  margin: 2rem auto;
}

.forecast-switch {
  text-transform: uppercase;
  border: none;
  background: none;
  color: #fff;
  font-size: 1.5rem;
}

.forecast-switch:hover {
  cursor: pointer;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}

.active {
  border-bottom: 0.2rem solid #fff;
}

.add-margin {
  margin-top: 7rem;
}

@media (max-width: 575px) {
  #weather-header {
    text-align: center;
  }
  #current-weather-details {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 2rem;
    padding: 1rem 0;
  }

  #forecast-switches {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 576px) {
  #weather-header {
    text-align: center;
  }

  #current-weather {
    grid-template-columns: 1fr;
  }

  #current-weather-main {
    justify-content: space-evenly;
  }

  #current-weather-details {
    grid-template-columns: repeat(2, 1fr);
    margin-bottom: 2rem;
    padding: 1rem 0;
  }

  .forecast-grid {
    grid-template-columns: 1fr;
  }

  .forecast-grid-item {
    flex-direction: row;
  }

  #forecast-switches {
    grid-template-columns: 1fr 1fr;
  }
}

@media (min-width: 768px) {
  #weather-header {
    margin-left: 2rem;
  }
}

@media (min-width: 992px) {
  #weather-header {
    text-align: left;
  }
  #current-weather {
    grid-template-columns: 0.75fr 1fr;
  }

  #current-weather-main {
    justify-content: space-around;
  }

  #current-weather-details {
    grid-template-columns: repeat(3, 1fr);

    border-left: 0.1rem solid #aaa;
  }

  .forecast-grid {
    grid-template-columns: repeat(7, 1fr);
  }

  .forecast-grid-item {
    flex-direction: column;
  }

  #forecast-switches {
    grid-template-columns: 10rem 10rem;
  }
}

