#results {
  width: 100%;
  position: absolute;
  overflow: hidden;
  border-top: 0.1rem solid #eee;
  background-color: #fff;
  border-radius: 0 0 0.5rem 0.5rem;
  z-index: 3;
}

.result {
  background: #fff;
  padding: 1rem;
  list-style: none;
}

.result:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
  cursor: pointer;
}

.selected {
  -webkit-filter: brightness(90%);
          filter: brightness(90%);
}

#partial-list {
  width: 100%;
  position: absolute;
  overflow: hidden;
  background-color: #fff;
  border-top: 0.1rem solid #eee;
  border-radius: 0 0 0.5rem 0.5rem;
  z-index: 3;
}

#partial-heading {
  padding: 1rem;
  background: #eee;
  font-size: 1rem;
  font-weight: 700;
}

.partial-result {
  padding: 1rem;
  font-size: 1rem;
  list-style: none;
}

.partial-result:hover {
  cursor: pointer;
  background-color: #eee;
}

#inner-button {
  margin-top: 0;
  border-radius: 0;
}

#search-form {
  align-self: center;
  justify-self: center;
  width: 100%;
  max-width: 36rem;
  margin-bottom: 3rem;
  padding: 1rem;
}

#search-heading {
  margin: 2rem auto;
  color: #fff;
  font-size: 4rem;
  font-weight: 700;
  text-align: center;
}

#search-bar {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#search-results {
  position: relative;
  width: 100%;
}

#input-container {
  width: 100%;
  display: flex;
  overflow: hidden;
  box-shadow: 0 8px 6px -6px #222;
}

#search-input {
  position: relative;
  width: 100%;
  border: none;
  padding: 1rem;
  font-size: 1rem;
  outline: 0;
}

.search-button {
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  margin-top: 2rem;
  font-weight: 600;
  border: none;
  border-radius: 0.5rem;
